.page-error {
    margin-bottom: 60px;
    margin-top: 80px;
    display: flex;
    align-items: center;
    flex-direction: row;

    &__title {
        font-family: $bold-f; }

    &__subtitle {
        font-size: 28px; }

    &-404 {
        position: relative;
        margin-top: 110px;
        padding-top: 0;

        &__text-wrap {
            margin-left: 110px;
            margin-right: 110px;
            padding-bottom: 400px; }

        &__title {
            margin-bottom: 20px;
            font-size: 48px; }

        &__subtitle {
            width: 365px; }

        &__img {
            width: 940px;
            position: absolute;
            top: 0;
            right: 30px; } }

    &-500 {
        margin: 60px 15px;
        align-items: center;
        justify-content: center;

        &__title {
            margin-top: 0;
            margin-bottom: 40px;
            font-size: 48px;
            text-align: center; }

        &__subtitle {
            max-width: 450px;
            text-align: center;
            margin: 0 auto; }

        &__img {
            display: block;
            width: 100%;
            max-width: 800px;
            margin-bottom: 60px; } } }

.maintenance-mode {

    &__header {
        margin-top: 70px;
        margin-bottom: 170px; }

    &__main {
        position: relative;
        min-height: 700px; }

    &__logo {
        display: block;
        width: 147px; }

    &__title {
        max-width: 350px;
        margin-top: 0;
        margin-bottom: 30px;
        font-size: 56px;
        font-family: $bold-f; }

    &__text-wrap {
        max-width: 350px; }

    &__img {
        width: 800px;
        position: absolute;
        top: 0;
        right: 0; } }
